<template>
  <el-container class="home-container">
    <el-header class="home-header">
      <div class="header-left">
        <img src="@/assets/heima.png" alt="" />
        <span>电商后台管理系统</span>
      </div>
      <el-button type="info" @click="exitLogin" class="home-right"
        >退出</el-button
      >
    </el-header>
    <el-container>
      <el-aside :width="isCollapse ? '40px' : '200px'" class="home-aside">
        <div class="aside-collapse" @click="toggleCollapse">|||</div>
        <el-menu
          :collapse="isCollapse"
          background-color="#333744"
          text-color="#fff"
          active-text-color="#ffd04b"
          unique-opened
          class="menu"
          router
          :default-active="activeMenu"
        >
          <!-- 一级导航 -->
          <el-submenu
            :index="menu.id + ''"
            v-for="menu in menuList"
            :key="menu.id"
          >
            <template slot="title">
              <!-- 图标 -->
              <i :class="iconList[menu.id]"></i>
              {{ menu.authName }}
            </template>
            <!-- 二级导航 -->
            <el-menu-item
              :index="'/' + item.path + ''"
              v-for="item in menu.children"
              :key="item.id"
              @click="getMenuActive('/' + item.path)"
            >
              <template slot="title">
                <i class="el-icon-menu"></i>{{ item.authName }}
              </template>
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </el-aside>
      <el-main class="home-main">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "HomePage",
  data() {
    return {
      menuList: [],
      iconList: {
        125: "iconfont icon-yonghu",
        103: "iconfont icon-tijikongjian",
        101: "iconfont icon-shangpin",
        102: "iconfont icon-danju",
        145: "iconfont icon-baobiao",
      },
      isCollapse: false,
      activeMenu: "",
    };
  },
  created() {
    this.getMeauList();
    this.activeMenu = window.sessionStorage.getItem("active");
  },
  methods: {
    exitLogin() {
      window.sessionStorage.clear();
      this.$router.push({ path: "/login" });
    },
    async getMeauList() {
      const { data } = await this.$http.get("menus");
      if (data.meta.status != 200) {
        return data.meta.message;
      }
      this.menuList = data.data;
    },
    toggleCollapse() {
      this.isCollapse = !this.isCollapse;
    },
    getMenuActive(path) {
      window.sessionStorage.setItem("active", path);
      this.activeMenu = path;
    },
  },
};
</script>

<style scoped lang='less'>
.home-container {
  height: 100%;
  color: #fff;
}
.home-header {
  background-color: rgb(56, 56, 70);
  display: flex;
  justify-content: space-between;
  padding-left: 0;
  align-items: center;

  .header-left {
    display: flex;
    align-items: center;
    span {
      font-size: 24px;
      margin-left: 20px;
    }
  }
}
.home-aside {
  background-color: #333744;
  .aside-collapse {
    height: 30px;
    line-height: 30px;
    text-align: center;
    letter-spacing: 0.2em;
    cursor: pointer;
    color: #bbb;
    background-color: #333756;
  }
  .menu {
    border: 0;
  }
}
.home-main {
  background-color: rgba(251, 250, 252, 0.913);
  color: #000;
}
</style>