import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)
import LoginPage from '@/components/login.vue'
import HomePage from '@/components/home.vue'
import WelCome from '../pages/welcome.vue'
import UsersList from '@/pages/users.vue'
import RightsList from '../pages/rights.vue'
import RolesList from '../pages/roles.vue'
import GoodsList from '../pages/goods.vue'
import OrdersList from '../pages/orders.vue'
import ReportsList from '../pages/reports'
import ParamsList from '../pages/params'
import CateList from '../pages/categories.vue'
const router = new VueRouter({
    routes: [
        {
            path: '/',
            component:LoginPage
        },
        {
            path: '/login',
            component:LoginPage
        },
        {
            name:'home',
            path: '/home',
            component: HomePage,
            redirect:'/welcome',
            children: [
                {
                    path: '/welcome',
                    component:WelCome
                },
                {
                    path:'/users',
                    component:UsersList
                },
                {
                    path:'/rights',
                    component:RightsList
                },
                {
                    path:'/roles',
                    component:RolesList
                },
                {
                    path:'/goods',
                    component:GoodsList
                },
                {
                    path:'/categories',
                    component:CateList
                },
                {
                    path:'/orders',
                    component:OrdersList
                },
                {
                    path:'/params',
                    component:ParamsList
                },
                {
                    path:'/reports',
                    component:ReportsList
                }
            ]
        },
        
    ],
})
router.beforeEach((to, from, next) => {
    // to and from are both route objects. must call `next`.
    // console.log(next)
    if (to.path == '/login') return next();
    const result = window.sessionStorage.getItem('token');
    if (!result)
        return next('/login')
    next();
})
export default router