<template>
  <div class="login_box">
    <div class="box_cen">
      <div class="login">
        <img src="../assets/logo.png" alt="" />
      </div>
      <div class="input">
        <el-form :model="loginForm" :rules="rules" ref="loginFormRef">
          <el-form-item prop="username">
            <el-input
              prefix-icon="iconfont icon-yonghu"
              v-model="loginForm.username"
            >
            </el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              type="password"
              prefix-icon="iconfont icon-tianchongxing-"
              v-model="loginForm.password"
            ></el-input>
          </el-form-item>
          <el-form-item class="btn">
            <el-button type="primary" @click="getLogin">登录</el-button>
            <el-button type="info" @click="resetLoginForm('loginFormRef')"
              >重置</el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginPage",
  data() {
    return {
      loginForm: {
        username: "admin",
        password: "123456",
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 5, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 6,
            max: 11,
            message: "长度在 6 到 11 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    resetLoginForm(formName) {
      this.$refs[formName].resetFields();
    },
    getLogin() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) {
          return;
        }
        const result = await this.$http.post("login", this.loginForm);
        // console.log(result);
        if (result.data.meta.status == 200) {
          this.$message({ type: "success", message: "登陆成功" });
          window.sessionStorage.setItem("token", result.data.data.token);
          this.$router.push({
            path: "/home",
          });
        } else {
          this.$message({ message: "登陆失败", type: "error" });
        }
      });
    },
  },
};
</script>

<style scoped lang="less">
.login_box {
  height: 100%;
  .box_cen {
    width: 550px;
    height: 330px;
    background-color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .login {
      width: 120px;
      height: 120px;
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      background-color: #fff;
      border-radius: 50%;
      padding: 10px;
      box-shadow: 0px 0px 10px #bbb;

      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        background-color: rgb(243, 241, 241);
      }
    }
    .input {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 20px;
      box-sizing: border-box;

      .btn {
        display: flex;
        justify-content: right;
      }
    }
  }
}
</style>