<template>
  <div>
    <el-breadcrumb separator=">">
      <el-breadcrumb-item :to="{ path: '>' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item><a href="/">用户管理</a></el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <div class="card-sear">
        <el-input
          placeholder="请输入搜索内容"
          class="input-content"
          v-model="queryData.query"
        ></el-input>
        <el-button
          icon="el-icon-search"
          class="btn-search"
          @click="getUserList"
        ></el-button>
        <el-button type="primary" @click="addUsers = true">添加用户</el-button>
      </div>
      <el-table :data="tableData" style="width: 100%; margin: 20px 0" border>
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="username" label="姓名"> </el-table-column>
        <el-table-column prop="email" label="邮箱"> </el-table-column>
        <el-table-column prop="mobile" label="电话"> </el-table-column>
        <el-table-column prop="role_name" label="角色"> </el-table-column>
        <el-table-column label="状态" prop="mg_state">
          <template slot-scope="scope">
            <el-switch
              v-model="scope.row.mg_state"
              @change="handleState(scope.row)"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="230px">
          <el-button type="primary" icon="el-icon-edit"></el-button>
          <el-button type="danger" icon="el-icon-delete"></el-button>
          <el-tooltip
            class="item"
            effect="dark"
            content="分配角色"
            placement="top"
          >
            <el-button type="warning" icon="el-icon-setting"></el-button>
          </el-tooltip>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
        :page-sizes="[2, 4, 6, 8]"
        :page-size="100"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
      <!-- 添加用户提示框 -->
      <el-dialog title="添加用户" :visible.sync="addUsers" width="50%">
        <el-form :model="usersData" :rules="usersRule">
          <el-form-item label="用户名" label-width="80px" prop="username">
            <el-input v-model="usersData.username"></el-input>
          </el-form-item>
          <el-form-item label="密码" label-width="80px" prop="password">
            <el-input v-model="usersData.password"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" label-width="80px" prop="email">
            <el-input v-model="usersData.email"></el-input>
          </el-form-item>
          <el-form-item label="手机" label-width="80px">
            <el-input v-model="usersData.mobile"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addUsers = false">取 消</el-button>
          <el-button type="primary" @click="addUsers = false">确 定</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "UsersList",
  data() {
    var validateEmail = (rule, value, callback) => {
      var reg = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
      console.log(rule);
      if (!reg.test(value)) {
        return callback(new Error("您输入的邮箱不符"));
      } else {
        return callback();
      }
    };
    return {
      tableData: [
        {
          id: 0,
          username: "",
          email: "",
          mobile: "",
          role_name: "",
          mg_state: true,
        },
      ],
      queryData: {
        query: "",
        pagenum: "1",
        pagesize: "2",
      },
      total: 1,
      addUsers: false,
      usersData: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      usersRule: {
        username: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
        ],
        password: [
          { required: true, message: "用户密码不能为空", trigger: "blur" },
        ],
        email: [{ validator: validateEmail, trigger: "blur" }],
      },
    };
  },
  // 页面还未加载
  created() {
    this.getUserList();
  },
  methods: {
    // 监听switch的状态,修改用户状态
    async handleState(user) {
      // console.log(Object.prototype.toString.call(user.id));
      const { data: res } = await this.$http.put(
        `users/${user.id}/state/${user.mg_state}`
      );
      if (res.meta.status !== 200) {
        this.tableData.mg_state = !this.tableData.mg_state;
        return this.$message.error("修改状态失败！");
      }
      this.tableData.mg_state = res.data.mg_state;
      this.$message.success("修改用户状态成功！");
    },
    async getUserList() {
      const { data: res } = await this.$http.get("/users", {
        params: this.queryData,
      });
      // console.log(res);
      // 获取用户列表失败
      if (res.meta.status != 200) return;
      // 获取用户列表成功，渲染页面
      this.tableData = res.data.users;
      this.total = res.data.total;
    },
  },
};
</script>

<style scoped lang = 'less'>
.box-card {
  margin-top: 20px;

  .card-sear {
    display: flex;

    .input-content {
      width: 300px;
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }

    .btn-search {
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
    }
  }
}
</style>